function HeaderButton(props) {
  let cls = "header-button";
  if (props.type === "big") {
    cls += " header-button-big";
  }
  const color = props.color || "blue";

  return (
    <button style={{ color: color }} className={cls}>
      {props.children}
    </button>
  );
}

